/**
 * Toggle Switch ( replaces checkbox on admin panels )
 */
.llms-switch {
  position: relative;
}
.llms-switch .llms-toggle {
  position: absolute;
  margin-left: -9999px;
  visibility: hidden;
}
.llms-switch .llms-toggle + label {
  box-sizing: border-box;
  display: block;
  position: relative;
  cursor: pointer;
  outline: none;
  user-select: none;
}
.llms-switch input.llms-toggle-round + label {
  border: 2px solid #6c7781;
  border-radius: 10px;
  height: 20px;
  width: 36px;
}
.llms-switch input.llms-toggle-round + label:before,
.llms-switch input.llms-toggle-round + label:after {
  box-sizing: border-box;
  content: "";
  display: block;
  position: absolute;
  transition: background 0.4s;
}
.llms-switch input.llms-toggle-round:checked + label {
  border-color: #11a0d2;
  background-color: #11a0d2;
}
.llms-switch input.llms-toggle-round + label:after {
  height: 12px;
  left: 2px;
  top: 2px;
  background-color: #6c7781;
  border-radius: 50%;
  transition: margin 0.4s;
  width: 12px;
  z-index: 3;
}
.llms-switch input.llms-toggle-round:checked + label:after {
  background-color: #fefefe;
  margin-left: 16px;
}
.llms-switch input.llms-toggle-round + label:before {
  height: 8px;
  top: 4px;
  border: 1px solid #6c7781;
  border-radius: 50%;
  right: 4px;
  width: 8px;
  z-index: 2;
}
.llms-switch input.llms-toggle-round:checked + label:before {
  border-color: #fefefe;
  border-radius: 0;
  left: 6px;
  right: auto;
  width: 2px;
}

